export const required = v => !!v || 'Required field.'

export const isNumber = v => !isNaN(v) || 'Only contains numbers.'

export const email = v => {
  var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return (re.test(String(v).toLowerCase())) || 'Not valid email address.';
}

export const ipv4 = v => /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/.test(v) || 'Invalid IP address.'

export const min6Char = v => {
  if (v === undefined) return !!v ;
  const num = 6;
  return v.length >= num || `Minimum ${num} characters at least.`
}

