<template>
  <v-form ref="form" v-model="valid" @submit.prevent="onSubmit">
    <v-container>
      <v-row>
        <v-col cols="6">
          <v-text-field
            label="Brand"
            placeholder="Brand"
            v-model="arrow.brand"
            :rules="rules.brand"
            autofocus
          />
        </v-col>

        <v-col cols="6">
          <v-text-field
            label="Model"
            placeholder="Model"
            v-model="arrow.model"
            :rules="rules.model"
          />
        </v-col>
      </v-row>

      <v-row justify="space-between">
        <v-col cols="2">
          <v-text-field
            label="Spin"
            placeholder="Spin"
            type="number"
            v-model="arrow.spin"
            :rules="rules.spin"
          />
        </v-col>
        <v-col cols="2">
          <v-text-field
            label="Weight"
            placeholder="Weight"
            type="number"
            v-model="arrow.weight"
            :rules="rules.weight"
          />
        </v-col>

        <v-col cols="2">
          <v-text-field
            label="Head weight"
            placeholder="Head weight"
            type="number"
            v-model="arrow.head_weight"
            :rules="rules.head_weight"
          />
        </v-col>

        <v-col cols="2">
          <v-text-field
            label="Grain"
            placeholder="Grain"
            type="number"
            v-model="arrow.grain"
            :rules="rules.grain"
          />
        </v-col>
        <v-col cols="2">
          <v-text-field
            label="Length"
            placeholder="Length"
            type="number"
            v-model="arrow.length"
            :rules="rules.length"
          />
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12">
          <v-textarea label="Comment" placeholder="Comment" v-model="arrow.comment"></v-textarea>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12">
          <v-btn type="submit" color="primary" class="white--text mr-4" :disabled="!valid">Save</v-btn>
          <v-btn class="mr-4" @click="onCancel">Cancel</v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
import { required, isNumber } from "@/libs/validations";

export default {
  props: ["item"],
  mounted() {
    if (this.item) this.arrow = { ...this.item };
  },
  data() {
    return {
      valid: false,
      arrow: {
        brand: "",
        model: "",
        weight: "",
        head_weight: "",
        spin: "",
        grain: "",
        length: "",
        comment: ""
      },
      rules: {
        brand: [required],
        model: [required],
        weight: [required, isNumber],
        head_weight: [required, isNumber],
        spin: [required, isNumber],
        grain: [required, isNumber],
        length: [required, isNumber]
      }
    };
  },
  methods: {
    async onSubmit() {
      this.$emit("onSubmit", { ...this.arrow });
      this.resetForm();
    },

    async onCancel() {
      this.$emit("onCancel");
      this.resetForm();
    },

    resetForm() {
      this.$refs.form.resetValidation();
    }
  }
};
</script>
