<template>
  <v-layout row wrap>
    <v-flex xs12 pa-3>
      <Panel title="Bow arrows">
        <template v-slot:buttons>
          <v-tooltip bottom key="create">
            <template v-slot:activator="{ on }">
              <v-btn
                v-on="on"
                fab
                dark
                small
                bottom
                right
                color="green"
                class="elevation-4"
                @click="OPEN_CREATE_DIALOG"
              >
                <v-icon>add</v-icon>
              </v-btn>
            </template>
            <span>Create new arrow</span>
          </v-tooltip>
        </template>

        <Table :headers="table.headers" :items="items" searchable>
          <template v-slot:actions="{ item }">
            <v-btn text icon color="primary" @click="OPEN_EDIT_DIALOG(item)">
              <v-icon>edit</v-icon>
            </v-btn>
            <v-btn text icon color="red" @click="OPEN_DELETE_DIALOG(item)">
              <v-icon>delete</v-icon>
            </v-btn>
          </template>
        </Table>

        <Dialog :show="createDialogOpened" title="New arrow" color="green" width="800">
          <BowArrowForm @onSubmit="createItem" @onCancel="CLOSE_CREATE_DIALOG" />
        </Dialog>

        <Dialog :show="editDialogOpened" title="Edit arrow" color="primary" width="800">
          <BowArrowForm :item="current" @onSubmit="editItem" @onCancel="CLOSE_EDIT_DIALOG" />
        </Dialog>

        <Dialog :show="deleteDialogOpened" title="Delete arrow" color="error" width="400">
          <p>Are you sure, do you want to delete this item?</p>
          <v-btn ma-2 color="error" @click="deleteItem(current)">Delete</v-btn>
          <v-btn ma-2 @click="CLOSE_DELETE_DIALOG">Cancel</v-btn>
        </Dialog>
      </Panel>
    </v-flex>
  </v-layout>
</template>

<script>
import Panel from "@/components/Panel";
import Table from "@/components/Table";
import Dialog from "@/components/Dialog";
import BowArrowForm from "@/components/forms/BowArrowForm";
import { mapState, mapMutations, mapActions } from "vuex";

export default {
  mounted() {
    this.$store.dispatch("bowArrowModule/fetchItems");
  },

  components: {
    Panel,
    Dialog,
    Table,
    BowArrowForm
  },
  computed: {
    ...mapState("bowArrowModule", [
      "items",
      "createDialogOpened",
      "editDialogOpened",
      "deleteDialogOpened",
      "current"
    ])
  },

  methods: {
    ...mapActions("bowArrowModule", ["editItem", "createItem", "deleteItem"]),
    ...mapMutations("bowArrowModule", [
      "OPEN_CREATE_DIALOG",
      "CLOSE_CREATE_DIALOG",
      "OPEN_EDIT_DIALOG",
      "CLOSE_EDIT_DIALOG",
      "OPEN_DELETE_DIALOG",
      "CLOSE_DELETE_DIALOG"
    ])
  },

  data() {
    return {
      table: {
        headers: [
          {
            text: "ID",
            align: "left",
            value: "id",
            sortable: true
          },
          {
            text: "Brand",
            align: "left",
            value: "brand",
            sortable: true
          },
          {
            text: "Model",
            align: "left",
            value: "model",
            sortable: true
          },
          {
            text: "Spin",
            align: "left",
            value: "spin",
            sortable: true
          },
          {
            text: "Grain",
            align: "left",
            value: "grain",
            sortable: true
          },
          {
            text: "Length",
            align: "left",
            value: "length",
            sortable: true
          },
          {
            text: "Weight",
            align: "left",
            value: "weight",
            sortable: true
          },
          {
            text: "Head weight",
            align: "left",
            value: "head_weight",
            sortable: true
          },

          { text: "", align: "right", value: "actions", sortable: false }
        ]
      }
    };
  }
};
</script>